.projectDiv {
  /* height: 770px; */
  padding: 0px 0px 30px 15px;
}

.projectName {
  margin: 10px 0 15px 0;
  font-size: 40px;
  display: inline-block;
}

.projectDate {
  display: inline;
  font-size: 14px;
  font-weight: bold;
  color: rgb(112, 112, 112);
  padding-left: 5px;
  margin: 0;
}

.projectContent {
  padding-left: 10px;
}

.projectContentTable {
  /* background-color: rgb(245, 245, 245); */
  width: 700px;
  padding: 2px;
}
.projectContentTr {
  margin: 0px;
  padding: 7px 2px 2px 5px;
  border-bottom: 1.5px solid rgb(146, 146, 146);
}
.projectLink {
  color: rgb(112, 112, 112);
  text-decoration: none;
}
.projectImg {
  margin: 10px 0 0 0;
  width: 700px;
}

